import {
	getIndexList,
	getStores
} from "../../util/index.js"

import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components:{
		
		uniPopup
	},
	data() {
		return {
			advertList: [],
			category: [],
			address:[],
			goods: [],
			stores: ''
		}
	},
	onLoad(options) {
		getIndexList().then(res => {
			if (res.data.code == 100) {
				console.log(res)
				this.advertList = res.data.info.advertList
				this.category = res.data.info.categoryList
				this.goods = res.data.info.bargainCommodityList
			} else {
				return uni.showToast({
					title: '获取数据失败'
				})
			}
		})
		uni.authorize({
			 scope: 'scope.userLocation',
			 success: ()=>{
				 uni.getLocation({
				 	success: res=>{
				 		getStores('24.6112244800','118.0471944800').then(res=>{
				 			console.log(res)
				 			this.address = res.data.map(item =>{
									item.memberByString = JSON.parse(item.memberByString)
									return item
								})
							   this.$refs.popup.open()
				 		})
				 	}
				 })
			 }
		})
	},
	methods: {
		moreSpecial(){
			uni.navigateTo({
				url: '/pages/moreSpecial/moreSpecial'
			});
		},
		toSerach() {
			uni.navigateTo({
				url: '/pages/search/search'
			});
		},
		goCategory(index, id) {
			uni.switchTab({
				url: '/pages/category/category'
			})
			uni.$emit('onceKinds', {
				index: index,
				id: id
			})
		},
		radioChange(e){
			this.stores = e.detail.value
			this.$refs.popup.close()
		}
	},
	computed: {
		newDemo() {
			if (this.category.length > 10) {
				let arr = []
				for (let i = 0; i < this.category.length; i += 10) {
					arr.push(this.category.slice(i, i + 10))
				}
				return arr
			} else {
				return this.category
			}
		}
	},
}
